.owl-padding {
    .owl-stage-outer {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }
}

.owl-prev, .owl-next {
    background: none !important;
    color: $dark !important;
    text-transform: uppercase;
    font-weight: $font-weight-normal;
    letter-spacing: 0.1em;
    transition: all 0.3s;
    &:hover {
        color: $brand-primary !important;
    }
}

.nav-center-custom {
    text-align: center;
    .owl-nav {
        display: inline-block;
        padding: 0.5rem 1.5rem;
        background: $light;
        margin: 0 auto;
    }
}
